}

.upload{
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 35px;
}